import React, { createContext, useContext, useReducer } from 'react';
import type { FC, Reducer } from 'react';
import { reducer } from './reducer';
import type { Action } from './reducer';
import type * as Types from './types';
import { NOOP } from '@react-page/editor';

export interface StateContext {
  token: string;
  user: Types.User;
}

export interface Store {
  state: StateContext;
  dispatch: React.Dispatch<Action>;
}

export const defaultState: StateContext = {
  user: {
    _id: '',
    email: '',
    phone: '',
    firstname: '',
    lastname: '',
    image: '',
  },
  token: '',
};

// eslint-disable-next-line @typescript-eslint/no-empty-function
const myContext = createContext<Store>({
  state: defaultState,
  dispatch: () => NOOP,
});

export const useStateContext = () => useContext(myContext);

type StateProviderType = {
  children: any;
};

export const StateProvider: FC<StateProviderType> = ({ children }) => {
  const [state, dispatch] = useReducer<Reducer<StateContext, Action>>(
    reducer,
    defaultState
  );
  return (
    <myContext.Provider value={{ state, dispatch }}>
      {children}
    </myContext.Provider>
  );
};
