import React from 'react';
import '../styles/globals.css';
import '../styles/styles.css';
import '@react-page/editor/lib/index.css';
import type { EmotionCache } from '@emotion/react';
import { CacheProvider } from '@emotion/react';
// import plugin css. We recommend to do that
// where you import the plugin itself,
// but there is a problem currently preventing us from do so,
// but that only applies to this example project
// see https://github.com/vercel/next.js/issues/19717
import '@react-page/plugins-background/lib/index.css';
import '@react-page/plugins-html5-video/lib/index.css';
import '@react-page/plugins-chat/lib/index.css';
import '@react-page/plugins-spacer/lib/index.css';
import '@react-page/plugins-social-feed/lib/index.css';
import '@react-page/plugins-button/lib/index.css';
import '@react-page/plugins-video/lib/index.css';
import '@react-page/plugins-image/lib/index.css';
import '@react-page/plugins-navigation/lib/index.css';
import '@react-page/plugins-footer/lib/index.css';
import '@react-page/plugins-slate/lib/index.css';
import '@react-page/plugins-collectibles/lib/index.css';
import 'katex/dist/katex.min.css';
import type { AppProps } from 'next/app';

import createEmotionCache from '../utils/createEmotionCache';
import { CssBaseline } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { isBrowser } from 'browser-or-node';
if (isBrowser) {
  require('codemirror/lib/codemirror.css');
  require('codemirror/theme/material.css');
  require('codemirror/mode/xml/xml');
  require('codemirror/mode/javascript/javascript');
  require('codemirror/mode/css/css');
}
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { StateProvider } from '../state';
import Head from 'next/head';

const clientSideEmotionCache = createEmotionCache();
const theme = createTheme();

function MyApp({
  Component,
  pageProps,
  emotionCache = clientSideEmotionCache,
}: AppProps & { emotionCache: EmotionCache }) {
  return (
    <CacheProvider value={emotionCache}>
      <Head>
        <title>MintChip Platform</title>
        <link rel="icon" href="/images/mc-favcon.jpg" />
        <meta name="description" content="MintChip Platform is ..." />
      </Head>

      <ThemeProvider theme={theme}>
        <CssBaseline />
        <StateProvider>
          <Component {...pageProps} />
        </StateProvider>
      </ThemeProvider>
      <ToastContainer theme="dark" />
    </CacheProvider>
  );
}

export default MyApp;
