import type { StateContext } from './state';
import type * as Types from './types';
import Cookies from 'js-cookie';

export enum ActionType {
  UPDATE_USER = 'Update User',
  UPDATE_TOKEN = 'Update Token',
}

export type Action =
  | { type: ActionType.UPDATE_USER; payload: Types.User }
  | { type: ActionType.UPDATE_TOKEN; payload: string };

export const reducer = (state: StateContext, action: Action) => {
  switch (action.type) {
    case ActionType.UPDATE_USER:
      Cookies.set('_id', action.payload._id);
      Cookies.set('email', action.payload.email);
      Cookies.set('phone', action.payload.phone);
      Cookies.set('firstname', action.payload.firstname);
      Cookies.set('lastname', action.payload.lastname);
      Cookies.set('image', action.payload.image);
      return { ...state, user: action.payload };
    case ActionType.UPDATE_TOKEN:
      Cookies.set('token', action.payload);
      return { ...state, token: action.payload };

    default:
      return state;
  }
};
